import React, {
    Component
} from 'react';
import PureRenderMixin from 'react-addons-pure-render-mixin'

import {
    Anchor,
    Row,
    Col,
    Breadcrumb,
    Button
} from 'antd';
import {
    HashRouter as Router,
    Route,
    Link,
    Redirect,
    Switch
} from 'react-router-dom'

import './style.scss';


import Headerlogo from 'images/headerlogo.png';
import Logo from 'images/logo.png';
import Jiarupingtai from 'images/jiarupingtai.png';
import Heyue from "images/heyue.png";
import Hezuo from "images/hezuo.png"


class Content extends Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this)
        this.state = {
            headerNav: [{
                name: ""
            }, {
                name: ""
            }, {
                name: ""
            }],

        }
    }

    toggleLogin = () => {
        this.props.logFn();
    }

    render() {
        return (
            <Row type="flex" justify="center" className="platform-content">
                <Col className="top" span={16}>
                    {/*<Breadcrumb separator=">">*/}
                        {/*<Breadcrumb.Item></Breadcrumb.Item>*/}
                        {/*<Breadcrumb.Item href=""></Breadcrumb.Item>*/}
                        {/*<Breadcrumb.Item href="">Application List</Breadcrumb.Item>*/}
                        {/*<Breadcrumb.Item>An Application</Breadcrumb.Item>*/}
                    {/*</Breadcrumb>*/}

                    <Row type="flex" justify="space-between" className="platform-intent">
                        <Col className="item" span={7}>
                            <img className="img" src={Jiarupingtai} title="" />
                            <p></p>
                            <p></p>
                            <Button className="button join" type="primary"><Link to="/platform/join"></Link></Button>
                        </Col>
                        <Col className="item" span={7}>
                            <img className="img" src={Heyue} title="" />
                            <p></p>
                            <p></p>
                            <Button className="button"></Button>
                        </Col>
                        <Col className="item" span={7}>
                            <img className="img" src={Hezuo} title="" />
                            <p></p>
                            <p></p>
                            <Button className="button"></Button>
                        </Col>
                    </Row>
                </Col>
            </Row>


        )
    }
}

export default Content;


